import React from 'react';
import s from './SignIn.module.scss';
import Auth0Methods from 'modules/Auth0Methods';
import { CustomInput } from 'components/CustomInput/CustomInput';
import { CustomButton } from 'components/CustomButton/CustomButton';
import arrowRight from 'images/arrowRight.svg';
import { Button } from 'types/types';
import google from 'images/google.svg';
import facebook from 'images/facebook.svg';
import linkedin from 'images/linkedin.svg';
import apple from 'images/apple.svg';
import { useNavigate } from 'react-router-dom';
import { AppRoutes } from 'pagesReact/appRouter/config';

const buttonArr: Button[] = [
  {
    methodName: 'Sign in with Google',
    image: google,
    connection: 'google-oauth2',
  },
  {
    methodName: 'Sign in with Facebook',
    image: facebook,
    connection: 'facebook',
  },
  {
    methodName: 'Sign in with Linkedin',
    image: linkedin,
    connection: 'linkedin',
  },
  {
    methodName: 'Sign in with Apple',
    image: apple,
    connection: 'apple',
  },
];

const SignIn = () => {
  const navigate = useNavigate();

  return (
    <div className={s.container}>
      <div className={s.header}>
        <div className={s.authName}>
          Sign in
        </div>
        <div className={s.authDescription}>
          Please login to continue to your account.
        </div>
      </div>
      <Auth0Methods
        buttons={buttonArr}
      />
      <div className={s.form}>
        <div className={s.textInputs}>
          <CustomInput
            placeholder={'Email'}
            background='grey'
          />
          <CustomInput
            placeholder={'Password'}
            type='password'
            background='grey'
          />
        </div>
        <div className={s.forgotContainer}>
          <div
            className={s.forgotText}
            onClick={() => navigate(AppRoutes.FORGOT_PASSWORD)}
          >
            Forgot password?
          </div>
        </div>
      </div>
      <CustomButton
        variant='primary'
        text={'Sign in'}
        image={arrowRight}
        imagePos='end'
      />
      <div className={s.footer}>
        <div className={s.text}>
          Need an account?
          <div
            className={s.textLink}
            onClick={() => navigate(AppRoutes.SIGNUP)}
          >
            Create one
          </div>
        </div>
      </div>
    </div>
  );
};

export { SignIn };